















import Vue from 'vue';
import Vuex from 'vuex';
import Article from '../types/article';
import ArticleLayout from '@/components/ArticleLayout.vue';
import DropdawnSelect from '@/components/DropdawnSelect.vue';
// import { extend } from 'vue/types/umd';
export default Vue.extend({
  name: 'ArticleView',

  components: {
    ArticleLayout,
    DropdawnSelect,
    },
  data() {
    return{
      curentArticle: {name: '', content: ''},
    };
  },

  methods: {
    loadTargetArticle(keyOfArticle: string) {
      const set: Element[] = Array.from(document.getElementsByClassName('link-active'));
      if (set) {
        set.forEach((element) => {
          element.classList.remove('link-active');
        });
      }
      if (event) {
         const article =  event.target as HTMLElement;
         article.classList.add('link-active');
      }
      const theArticle: Article = this.$store.state.mats.find( (el: Article) => el.name === keyOfArticle );
      this.$data.curentArticle =  theArticle;
    },
  },

  computed: {
    articlesSet(): string[] {
      const names: string[] = [];
      this.$store.state.mats.forEach((el: Article) => {
        names.push(el.name);
      });
      return names;
    },
  },

});
